/* First party */
import { Component } from 'react'
import { Grid, Row, Col } from 'react-styled-flexboxgrid'
import styled from 'styled-components'

/* Third party */
import data from '../characters.json'
import ResultCard from '../components/ResultCard'

const colors = {
    lightCoffee: '#dfd9d3',
    coffee: '#bfb3a8',
    darkCoffee: '#907a67'
}

const Container = styled.div`
    margin: 0 auto;
`

const Title = styled.h1`
    text-align: center;
`

const HeaderRow = styled(Row)`
    margin-top: 0rem;
`

const PaddedRow = styled(Row)`
    margin-top: 3rem;
`

const Name = styled.p`
    color: gray;
    font-size: 1.2rem;
    opacity: 0.8;
    padding-bottom: 0.3em;
    text-align: center;
    vertical-align: middle;

    &:hover {
        color: gray;
        cursor: pointer;
        opacity: 1;
        border-bottom: 1px solid gray;
    }
`

const Theme = styled(Grid)`
    font-family: 'NeueHaasUnicaPro-Regular';
    padding: 5rem;
    padding-top: 1rem;
`

export default class Home extends Component {
    constructor(props) {
        super(props)
        this.state = {
            selectedCharacter: '',
            films: []
        }
    }

    handleCharacterNameClick = (name, reqUrl) => {
        this.setState({
            films: [],
            selectedCharacter: name
        })
        this.fetchFilms(reqUrl)
        
    }

    async fetchFilms (reqUrl) {
        fetch(reqUrl)
        .then(res => res.json())
        .then(data => {
            const filmUrls = data.films
            filmUrls && filmUrls.map(url => {
                fetch(url)
                .then(res => res.json())
                .then(filmData => {
                    this.setState({
                        films: [...this.state.films, filmData]
                    })
                })
            })
        })
    }
    
    render () {
        // console.log('data: ', data.characters)
        const { handleCharacterNameClick } = this
        const { films } = this.state
        return (
            <Container>
                <Theme>
                    <Title> characters and films </Title>
                    <HeaderRow center='xs'>
                    { data.characters.map((char, i) => {
                        return (
                            <Col key={i} xs={12} sm={6} md={3}>
                                <Name onClick={() => handleCharacterNameClick(char.name, char.url)}> {char.name} </Name>    
                            </Col>
                        )
                    })}
                    </HeaderRow>
                    <PaddedRow start='xs'>
                        <Col>
                        { films.map((film, i) => {
                            return (
                                <Row>
                                    <Col key={i}>
                                        <ResultCard
                                            film={film}
                                        />
                                    </Col>
                                </Row>
                            )
                        })}
                        </Col>
                    </PaddedRow>
                </Theme>
            </Container>
        )
    }
}
